header{
    background-color: #000;
    height: 80px;
    font-size: 13px !important;
    z-index: 3;
}
header button{
    background-color: #ff4047 !important;
    border: none !important;
    border-radius: 15px !important;
    font-size: inherit !important;
}
header svg{
    cursor: pointer !important;
}
header svg:hover{
    filter: brightness(85%);

    
}
