
nav ul{
    list-style: none;

}
ul li{
    position: relative;
    height: 100%;
}
ul a{
    text-decoration: none;
    display: flex;
    align-items: center;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
}
nav img{
    width: 80px;
    filter: invert(100%) contrast(180%);
    

}

nav li{
    padding: 10px;
}
header button:hover{
    filter: brightness(95%);
}
ul li:hover{
    background-color: #292929;
    border-radius: 15px;
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
}
ul li::after{
    content: "";
    width: 0;
    height: 4px;
    background-color: #ff4047 ;
    display: block;
    position: absolute;
    bottom: 0px;
    border-radius: 5px;
    left: 0;
    transition: width .5s;
}
ul li:hover::after{
    width: 100%;
    
}

@media screen and (max-width: 767px) {
 
    nav ul{
        position: absolute;
        width: 100%;
        top: 80px;
        right: 0;
        background-color: #000 !important;
        height: calc(100vh - 80px);
    }
    nav ul li{
        width: 100%;
        display: flex;
        justify-content: center;
    }
}
@media screen and (min-width: 768px) and (max-width: 991px{
    nav ul li{
        font-size: 1px;
    }
}